//index.html

.banner{
    height: 92.9vh;
    background-image: url("../images/home-header.png");
    background-size: cover;
    background-position: center;
    .container{
        margin-right: 300px;
    }
    .banner-text{
        .btn{
            width: 18%;
            margin-left: auto;
        }
    }
}

@media(max-width:992px){
    .banner{
        .container{
            margin-right: 100px;
        }       
    }
}


@media(max-width:768px){
    .banner{
        background-image: url("../images/home-header-sm.png");
        .container{
            margin-right: 25px;
        }
    }
}

@media(max-width:375px){
    .banner{
        .container{
            margin-right: 0px;
        }
        h3{
            font-size: $font-size-base-xs;
            font-weight: 400;
        }
        h1{
            font-size: $font-size-base-m;
            font-weight: 700;
        }
        .banner-text{
            .btn{
                width: 47.7%;
            }
        }
    }
}

.description{
    padding: 80px 0;
    .description-list{
        .description-card{
            width: 23.6%;
            img{
                width: 32.7%;
                &:hover{
                    opacity: 100%;
                }
            }
            p{
                color: $primary-text-color;
            }
        }
    }
}

@media(max-width:768px){
    .description{
        padding: 80px 0 56px;
        .description-list{
            padding: 0 24px;
            .description-card{
                width: 48.4%;
                margin-bottom: 24px;
                img{
                    width: 29.7%;
                }
            }
        }
    }
}

@media(max-width:375px){
    .description{
        padding: 40px 0 24px;
        h2{
            font-size: $font-size-base-m;
            font-weight: 700;
        }
        .description-list{
            padding: 0 2px;
            .description-card{
                width: 100%;
                margin-bottom: 16px;
                img{
                    width: 28.8%;
                }
            }
        }
    }
}


