.contact-form{
    padding: 80px 0;
    .contact-form-content{
        width: 49.1%;
        margin: 0 auto;
    }
    form{
        input[type=text]{
            width: 100%;
            padding: 6px 12px;
            margin: 8px 0 16px;
            background: $thirdly-color;
            border: 1px solid #000;
        }
        textarea{
            width: 100%;
            padding: 6px 12px 106px;
            resize: none;
            margin-top: 8px;
            background: $thirdly-color;
            border: 1px solid #000;
            margin-bottom: 16px;
        }
    }
    .confirm{
        display: flex;
        align-items: center;
        &::before{
            content:"";
            width: 18px;
            height: 18px;
            border: 2px solid #000;
            border-radius: 2px;
            margin-right: 4px;
        }
    }
    .btn{
        width: 65.5%;
        margin: 0 auto;
    }
}

@media(max-width:768px){
    .contact-form{
        .contact-form-content{
            width: 77.6%;
        }
        .btn{
            width: 58.3%;
            margin: 0 auto;
        }
    }
}

@media(max-width:375px){
    .contact-form{
        padding: 40px 0;
        h2{
            font-size: $font-size-base-m;
            font-weight: 700;
            margin-bottom: 24px;
        }
        .contact-form-content{
            width: 100%;
        }
        .confirm{
            margin-bottom: 24px;
        }
        .btn{
            width: 100%;
        }
    }
}

