//page3
//allstore
.allStore{
    padding: 96px 0 80px;
    h1{
        font-size: $font-size-subtitle;
        font-weight: 700;
        padding-left: 16px;
        border-left: 8px solid #9F1213;
        margin-bottom: 32px;
    }
    .store-selection{
        p{
            font-size: $font-size-base-m;
        }
        select{
            width: 23.6%;
            font-size: 18px;
            line-height: 26px;
            padding: 6px 12px;
            option{
                font-size: 18px;
                line-height: 26px;
            }
        }
    }
    .store-card{
        width: 33%;
        h2{
            font-size: $font-size-base-m;
            font-weight: 400;
            padding: 8px 24px;
            border: 1px solid $primary-bdr-color;
        }
        .store-information{
            padding: 16px 24px;
            border: 1px solid $primary-bdr-color;
            margin-top: -1px;
            li{
                line-height: 30px;
            }
            span{
                font-size: $font-size-base-m;
            }
            a{
                color: #000;
                &:hover{
                    opacity: 80%;
                }
            }
        }
    }
}

@media (max-width:768px){
    .allStore{
        padding: 80px 24px 56px;
        .store-selection{
            select{
                width: 43.7%;
            }
        }
        .store-card{
            width: 50%;
            margin-bottom: 48px;
        }
    }
}

@media(max-width:375px){
    .allStore{
        padding: 40px 0 16px;
        h1{
            font-size: $font-size-base-m;
            margin-bottom: 24px;
        }
        .store-selection{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 24px;
            p{
                font-size: $font-size-base-xs;
                margin-right: 8px;
            }
            select{
                width: 78.6%;
            }
        }
        .store-card{
            width: 100%;
            margin-bottom: 24px;
        }
    }
}

